$background: #272727;

.header {
  background-color: rgba($background, .8);
  padding: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 999;

  &__menu {
    ul {
      display: flex;
    }
  }

  &__burger-menu {
    cursor: pointer;
    border: 0;
    background-color: transparent;
    display: none;

    div {
      width: 35px;
      height: 5px;
      background-color: #FFF;
      margin: 6px 0;
    }
  }

  &__menu-item {
    & + .header__menu-item {
      margin-left: 50px;
    }

    a {
      text-transform: uppercase;
      font-size: 20px;
      color: #fff;
      text-decoration: none;

      &:focus,
      &:active {
        color: #F07C79;
      }
    }
  }

  @media only screen and (max-width: 768px) {
    padding: 10px 20px;

    svg {
      width: 90px;
    }

    .header__burger-menu {
      display: block;
    }

    .header__menu {
      position: absolute;
      top: 100%;
      left: 0;
      right: 0;
      display: none;
      background-color: rgb(39, 39, 39);
      padding: 20px;

      ul {
        flex-direction: column;
      }
    }

    .header__menu-item {
      padding: 10px;

      & + .header__menu-item {
        margin-left: 0;
      }
    }

    &.header--menu-open {
      .header__menu {
        display: block;
      }
    }
  }
}