.timeline {
  background: linear-gradient(#272727, #464646);
  counter-reset: nodeIndex;

  &__container {
    padding: 160px 0 105px;
  }

  &__node {
    position: relative;
    padding: 70px;
    margin: 0 auto;
    max-width: 410px;

    &.timeline__node--open {
      .timeline__node-content {
        max-height: 500px;
        opacity: 1;
      }
    }

    & + .timeline__node {
      margin-top: -10px;
    }

    &:first-child {
      &::after {
        content: '';
        position: absolute;
        top: -5px;
        left: 0;
        bottom: 0;
        width: 50%;
        border-top: 10px solid #fff;
      }
    }

    &.timeline__node--blue {
      .timeline__node-icon {
        background-color: rgb(68, 224, 220);

        &:hover {
          box-shadow: 0 0 0 24px rgba(68, 224, 220, 0.3);
        }
      }

      .timeline__node-title,
      .timeline__node-icon-title {
        color: rgb(68, 224, 220);
      }
    }


    &.timeline__node--yellow {
      .timeline__node-icon {
        background-color: rgb(255, 237, 101);

        &:hover {
          box-shadow: 0 0 0 24px rgba(255, 237, 101, 0.3);
        }
      }

      .timeline__node-title,
      .timeline__node-icon-title {
        color: rgb(255, 237, 101);
      }
    }


    &.timeline__node--red {
      .timeline__node-icon {
        background-color: rgb(240, 124, 121);

        &:hover {
          box-shadow: 0 0 0 24px rgba(240, 124, 121, 0.3);
        }
      }

      .timeline__node-title,
      .timeline__node-icon-title {
        color: rgb(240, 124, 121);
      }
    }

    &.timeline__node--right {
      .timeline__node-icon-title {
        right: unset;
        left: calc(100% + 50px);
        align-items: flex-start;
      }

      .timeline__node-icon {
        left: unset;
        right: 0;
        transform: translateY(-50%) translateX(50%);
      }

      &::before {
        border-right: 0;
        border-left: 10px solid #fff;
        left: -5px;
        border-radius: 60px 0 0 60px;
      }
    }

    .timeline__node-title {
      color: #FFED65;
      font-size: 34px;
      font-weight: bold;
    }

    .timeline__node-paragraph {
      color: #fff;
      line-height: 1.5;
      margin-top: 27px;
    }

    .timeline__node-icon {
      border-radius: 50%;
      padding: 22px;
      border: 0;
      display: flex;
      position: absolute;
      top: 0;
      left: 0;
      transform: translateY(-50%) translateX(-50%);
      transition: box-shadow 0.15s ease-in-out;
      cursor: pointer;
      z-index: 1;
    }

    .timeline__node-icon-title {
      position: absolute;
      right: calc(100% + 50px);
      transform: translateY(-50%);
      top: 0;
      font-size: 80px;
      display: flex;
      flex-direction: column;
      align-items: flex-end;

      .timeline__node-title::before {
        counter-increment: nodeIndex;
        content: counter(nodeIndex) ".";
        font-size: 80px;
      }
    }

    .timeline__node-content {
      overflow: hidden;
      transition: max-height 0.6s ease-in-out, opacity 0.8s ease-in-out;
      max-height: 0;
      opacity: 0;
    }

    &::before {
      content: '';
      position: absolute;
      top: -5px;
      bottom: 5px;
      width: 50%;
      border-right: 10px solid #fff;
      border-top: 10px solid #fff;
      border-bottom: 10px solid #fff;
      border-radius: 0 60px 60px 0;
      left: calc(50% - 5px);
    }

    &:last-child {
      &::before {
        width: 36px;
        height: 36px;
        border-radius: 50%;
        left: 50%;
        top: 0;
        border: 0;
        z-index: 9;
        background: #fff;
        bottom: unset;
        transform: translateX(-50%) translateY(-50%);
      }
    }
  }

  @media only screen and (max-width: 1024px) {
    .timeline__node-icon-title {
      display: none;
    }
  }

  @media only screen and (max-width: 768px) {
    .timeline__container {
      padding: 50px 0;
    }

    .timeline__node {
      padding: 40px;
      max-width: 100%;
      margin-left: 40px;
      margin-right: 40px;

      .timeline__node-title {
        font-size: 20px;
      }

      .timeline__node-paragraph {
        font-size: 14px;
      }

      .timeline__node-icon {
        img {
          width: 25px;
        }
      }
    }
  }
}