.news {
  background: url('../../assets/ONDE COLORATE/onda-bianco-grigio.svg') center calc(100% + 2px) / contain no-repeat;

  &__container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(275px, 1fr));
    grid-gap: 20px;
    padding: 60px 0 0;
  }

  &__content {
    padding: 23px;
    position: absolute;
    bottom: 0;
  }

  &__date {
    font-size: 14px;
    color: #838080;
  }

  article {
    background-color: #F3F3F3;
    display: flex;
    flex-direction: column;
    text-decoration: none;
    height: 480px;
    position: relative;

    a {
      height: 100%;
    }

    &::before {
      content: '';
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      top: 0;
      opacity: 0;
      background: linear-gradient(RGBA(237, 101, 98, 0.3), #ED6562);
      transition: opacity 0.5s ease;
    }

    &:hover {
      p {
        max-height: 300px;

        &::before {
          content: none;
        }
      }

      &::before {
        opacity: 1;
      }

      h3,
      p,
      .news__date {
        color: #fff;
      }
    }

    h3 {
      color: #ED6562;
      font-size: 24px;
      line-height: 1.25;
      margin-top: 12px;
    }

    p {
      margin-top: 12px;
      font-size: 20px;
      position: relative;
      line-height: 30px;
      max-height: calc(30px * 3);
      overflow: hidden;
      padding-right: 1rem;
      text-align: justify;
      transition: max-height 0.5s ease;

      &::before {
        position: absolute;
        content: "...";
        bottom: 0;
        right: 0;
        animation: ellipsis-anim ease 0.5s;
      }
    }

    .news__image {
      background-size: cover;
      background-position: center;
      height: 50%;
      width: 100%;
      display: block;
    }
  }

  .question {
    padding: 112px 0 180px;
  }

  @media only screen and (max-width: 768px) {
    article {
      h3 {
        font-size: 16px;
      }

      p {
        font-size: 14px;
      }
    }
  }
}

@keyframes ellipsis-anim {
  0%, 99% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}