$slider-transition: opacity 0.8s ease-in-out;

.slider {
  position: relative;
  counter-reset: slider;
  height: 810px;

  .slider__slide {
    transition: $slider-transition;
    opacity: 0;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    background-size: cover;
    background-position: center;
    display: flex;
    align-items: flex-end;
    padding: 70px 20%;

    &.slider__slide--active {
      opacity: 1;
      z-index: 1;
    }
  }

  &__text {
    color: #FFF;
    max-width: 550px;
    word-break: break-word;

    h2 {
      font-size: 60px;
      line-height: 1.33;
      padding-bottom: 20px;
    }

    p {
      font-size: 20px;
      padding-bottom: 40px;
      line-height: 1.2;
    }

    button {
      background-color: #F07C79;
      border-radius: 16px;
      padding: 18px 20px;
      font-size: 20px;
      text-transform: uppercase;
      border: 0;
      color: #FFF;
      cursor: pointer;
      transition: box-shadow 0.15s ease-in-out;

      &:hover {
        box-shadow: 0 0 0 10px rgba(240, 124, 121, 0.4);
      }
    }
  }

  &__buttons-container {
    display: flex;
    flex-direction: column;
    position: absolute;
    left: 0;
    top: 20%;
    z-index: 3;
  }

  &__button {
    background-color: #F07C79;
    color: #fff;
    font-size: 20px;
    padding: 23px;
    opacity: 0.5;
    border: 0;
    counter-increment: slider;
    cursor: pointer;
    transition: $slider-transition;

    &::before {
      content: counter(slider);
    }

    &.slider__button--active {
      opacity: 1;
    }
  }

  &__arrow {
    position: absolute;
    top: 55%;
    background: transparent;
    border: 0;
    cursor: pointer;
    z-index: 3;

    &.slider__arrow--prev {
      left: 80px;
    }

    &.slider__arrow--next {
      right: 80px;
    }
  }

  @media only screen and (max-width: 768px) {
    height: 400px;

    .slider__buttons-container {
      display: none;
    }

    .slider__slide {
      padding: 20px 40px;
    }

    .slider__arrow {
      svg {
        width: 15px;
      }

      &--prev {
        left: 0;
      }

      &--next {
        right: 0;
      }
    }

    .slider__text {
      h2 {
        font-size: 30px;
      }

      p,
      button {
        font-size: 14px;
      }
    }
  }
}