body {
  font-family: 'Roboto', sans-serif;
}

section, footer {
  padding: 0 60px;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
}

.question {
  font-size: 60px;
  color: #ED6562;
  max-width: 800px;
  line-height: 80px;

  &--right {
    margin-left: auto;
    text-align: right;
  }
}

@media only screen and (max-width: 768px) {
  section, footer {
    padding: 0 20px;
  }

  .question {
    font-size: 30px;
    line-height: 1.2;
  }
}