.footer {
  background-color: #272727;
  color: #FFF;
  font-size: 14px;

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    flex-wrap: wrap;
    padding: 80px 0 50px;
  }

  &__logo {
    margin-bottom: 30px;
  }

  &__terms + .footer__terms {
    margin-top: 25px;
  }

  &__social-icons {
    display: flex;

    li + li {
      margin-left: 16px;
    }
  }

  &__social-title {
    margin-bottom: 16px;
    padding-top: 30px;
  }

  @media only screen and (max-width: 768px) {
    font-size: 12px;

    .footer__logo {
      width: 90px;
    }
  }
}