.cta {
  background: url("../../assets/grafica sfondo.svg") center bottom/ contain no-repeat;

  &__container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(275px, 1fr));
    grid-gap: 20px;
    padding: 0 0 145px;
  }

  &__card {
    padding: 40px;
    text-align: center;
    border-radius: 20px;
    max-width: 500px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;

    .cta__card-title {
      font-size: 34px;
      border-bottom: 2px solid #FFF;
      text-transform: uppercase;
      padding-bottom: 20px;
      align-self: stretch;
    }

    img {
      padding: 40px 0;
    }

    .cta__card-content {
      padding-bottom: 24px;
      line-height: 1.5;
    }

    .cta__card-button {
      padding: 20px 15px;
      font-size: 20px;
      text-transform: uppercase;
      border: 0;
      border-radius: 20px;
      cursor: pointer;
      transition: box-shadow 0.15s ease-in-out;
    }

    &--pink {
      background-color: #EF6F6C;
      color: #FFF;

      .cta__card-button {
        background-color: #FFF;
        color: #EF6F6C;

        &:hover {
          box-shadow: 0 0 0 10px rgba(255, 255, 255, 0.4);
        }
      }
    }

    &:not(.cta__card--pink) {
      background-color: #FFF;
      box-shadow: 0 0 20px 10px rgba(0, 0, 0, 0.1);
    }

    &--yellow {
      .cta__card-title {
        color: #E2CA40;
        border-color: #E2CA40;
      }

      .cta__card-button {
        background-color: #E2CA40;
        color: #FFF;


        &:hover {
          box-shadow: 0 0 0 10px rgba(226, 202, 64, 0.4);
        }
      }
    }

    &--turquoise {
      .cta__card-title {
        color: #28AFB0;
        border-color: #28AFB0;
      }

      .cta__card-button {
        background-color: #28AFB0;
        color: #FFF;

        &:hover {
          box-shadow: 0 0 0 10px rgba(40, 175, 176, 0.4);
        }
      }
    }
  }

  @media only screen and (max-width: 768px) {
    .cta__container {
      padding: 0 0 50px;
    }

    .cta__card {
      .cta__card-title {
        font-size: 20px;
      }

      .cta__card-button,
      .cta__card-content {
        font-size: 14px;
      }
    }
  }
}

.cta-title {
  position: relative;

  &::before {
    content: '';
    left: 0;
    right: 0;
    top: 0;
    height: 1000px;
    z-index: -1;
    position: absolute;
    background: url("../../assets/ONDE COLORATE/onda-grigio-bianco.svg") center -2px / contain no-repeat;
  }

  .question {
    padding: 308px 0 100px;
  }

  @media only screen and (max-width: 768px) {
    .question {
      padding: 260px 0 50px;
    }
  }
}